<template>
    <div class="mv-box">
        <div>
            <el-card>
                <el-page-header @back="goBack">
                    <template #content>
                        {{song}} - {{singer}}
                        <el-tag v-if="def">{{def}}P</el-tag>
                    </template>
                </el-page-header>
                <el-divider />
                <component
                    :is="com"
                    class="video-player vjs-custom-skin"
                    :playsinline="true"
                    :options="playerOptions"
                    v-loading="loading"
                    element-loading-background="rgba(0, 0, 0, 0.5)"
                ></component>
            </el-card>
        </div>
        <div>
            <el-card v-if="hotComments" class="comments" v-loading="!hotComments">
                <h3>精彩评论</h3>
                <el-divider />
                <ul>
                    <li v-for="(item,index) in hotComments" :key="index">
                        <div>
                            <div>
                                <div
                                    class="userPic"
                                    :style="{'background-image':`url(${item.user.avatarUrl}?param=40y40)`}"
                                ></div>
                                <div>
                                    <h5>{{item.user.nickname}}</h5>
                                    <span>{{toDate(item.time)}}</span>
                                </div>
                            </div>
                            <div style="display:flex;align-items: center">
                                {{item.likedCount}}
                                <i
                                    class="ri-thumb-up-line"
                                    style="margin-left:5px"
                                ></i>
                            </div>
                        </div>
                        <p>{{item.content}}</p>
                        <el-divider />
                    </li>
                </ul>
            </el-card>
            <el-card class="comments" v-loading="!commpents">
                <h3>最新评论</h3>
                <el-divider />
                <ul>
                    <li v-for="(item,index) in commpents" :key="index">
                        <div>
                            <div>
                                <div
                                    class="userPic"
                                    :style="{'background-image':`url(${item.user.avatarUrl}?param=40y40)`}"
                                ></div>
                                <div>
                                    <h5>{{item.user.nickname}}</h5>
                                    <span>{{toDate(item.time)}}</span>
                                </div>
                            </div>
                            <div>
                                {{item.likedCount}}
                                <i class="ri-thumb-up-line"></i>
                            </div>
                        </div>
                        <p>{{item.content}}</p>
                    </li>
                </ul>
                <el-pagination
                    background
                    layout="total, sizes, prev, pager, next"
                    :total="total"
                    :page-size="limit"
                    @size-change="i => limit = i"
                    @current-change="i => offset = i - 1"
                    style="display:flex;"
                />
            </el-card>
        </div>
    </div>
</template>
<script setup>
import { defineComponent, ref, watch, onMounted, computed } from "vue";
import { videoPlayer } from "vue-video-player/src";
import "vue-video-player/src/custom-theme.css";
import "video.js/dist/video-js.css";
import * as axios from "axios";
axios.defaults.withCredentials = true;
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import { ElMessage } from "element-plus";
const store = useStore();
const router = useRouter();
const loading = ref(true);

const com = ref(null);
const id = ref("");
const mv = ref("");
const song = ref("");
const singer = ref("");
const def = ref(null);
const total = ref(0);
const limit = ref(20);
const offset = ref(0);
const hotComments = ref([]);
const commpents = ref([]);

const playerOptions = ref({
    //播放速度
    playbackRates: [0.5, 1.0, 1.5, 2.0],
    //如果true,浏览器准备好时开始放。
    autoplay: true,
    // 默认情况下将会消除任何音频。
    muted: false,
    // 导致视频一结束就重新开始。
    loop: false,
    // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
    preload: "auto",
    language: "zh-CN",
    // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
    aspectRatio: "16:9",
    // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
    fluid: true,
    sources: [
        {
            type: "video/mp4",
            src: ""
        }
    ],
    //你的封面地址
    poster: "",
    //允许覆盖Video.js无法播放媒体源时显示的默认信息。
    notSupportedMessage: "视频加载中",
    controlBar: {
        timeDivider: true,
        durationDisplay: true,
        remainingTimeDisplay: false,
        //全屏按钮
        fullscreenToggle: true
    }
});

const initMV = () => {
    try {
        axios
            .get(`${store.state.webService}/mv/url?id=${mv.value}`)
            .then(res => {
                def.value = res.data.data.r;
                playerOptions.value.sources[0].src = res.data.data.url;
                com.value = videoPlayer;

                loading.value = false;
            })
            .catch(err => {
                ElMessage.error("MV加载失败");
            });
    } catch (err) {
        console.log(err, "err");
    }
};

const initInfo = () => {
    try {
        axios
            .get(`${store.state.webService}/song/detail?ids=${id.value}`)
            .then(res => {
                let item = res.data.songs[0];
                playerOptions.value.poster = item.al.picUrl;
                song.value = item.name;
                singer.value = "";
                item.ar.forEach((i, j) => {
                    j == 0
                        ? (singer.value = i.name)
                        : (singer.value += ` / ${i.name}`);
                });
            })
            .catch(err => {
                ElMessage.error("歌曲加载失败");
            });
    } catch (err) {
        console.log(err, "err");
    }
};

const initComment = () => {
    hotComments.value = null;
    commpents.value = null;
    try {
        axios
            .get(
                `${store.state.webService}/comment/mv?id=${mv.value}&limit=${limit.value}&offset=${offset.value}`
            )
            .then(res => {
                total.value = res.data.total;
                commpents.value = res.data.comments;
                hotComments.value = res.data.hotComments;
            });
    } catch (err) {
        console.log(err, "err");
    }
};

const toDate = computed(() => {
    return function(val) {
        let time = new Date(val);
        let y = time.getFullYear();
        let mth = time.getMonth() + 1;
        let d = time.getDate();
        let h = time.getHours();
        let m = time.getMinutes();
        let s = time.getSeconds();
        mth = mth < 10 ? "0" + mth : mth;
        d = d < 10 ? "0" + d : d;
        h = h < 10 ? "0" + h : h;
        m = m < 10 ? "0" + m : m;
        s = s < 10 ? "0" + s : s;
        return `${y}年${mth}月${d}日 ${h}:${m}:${s}`;
    };
});

watch(
    router.currentRoute,
    val => {
        if (val.path.includes("mv")) {
            loading.value = true;
            if (!val.query.mv || !val.query.id) {
                ElMessage.error(
                    "未传入MV的id值，则无法播放，即将为您跳转回首页"
                );
                router.push("/");
                return false;
            }
            id.value = val.query.id;
            mv.value = val.query.mv;
            initInfo();
            initMV();
            initComment();
        }
    },
    {
        immediate: true,
        deep: true
    }
);

watch(
    limit,
    val => {
        console.log(val, "limit");
        initComment();
    },
    {
        deep: true
    }
);
watch(
    offset,
    val => {
        console.log(val, "offset");

        initComment();
    },
    {
        deep: true
    }
);
const goBack = () => {
    router.back();
};
</script>
 
<style lang="less" scoped>
.mv-box {
    @media (min-width: 1300px) {
        display: flex;
        flex: 1;
        height: 100%;
        flex-direction: row;
        & > div {
            flex: 1;
            width: 0;
            height: 100%;
            overflow-y: auto;
        }
        & > div:first-child {
            margin-right: 20px;
            flex: 1.5;
        }
    }
}
.el-card {
    margin-bottom: 20px;
}
.comments {
    ul {
        margin-bottom: 20px;
        li {
            padding: 0 15px;
            div:first-child {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                .userPic {
                    width: 40px;
                    height: 40px;
                    background-size: 100%;
                    border-radius: 50%;
                    margin-right: 5px;
                }
                h5 {
                    font-size: 16px;
                    font-weight: bold;
                }
                span {
                    font-size: 14px;
                    color: #aaa;
                }
            }
            p {
                margin: 5px 0 20px 45px;
            }
        }
    }
}
</style>